td {
	border-bottom: 1px solid #ccc;
	height: 33px;
	font-size: 12px;
	text-transform: uppercase;
	font-weight: 600;
	min-width: 50px
}

td,
th {
	color: #000;
	font-family: Montserrat, sans-serif
}

.border {
	border: 1px solid #0070f0;
	min-height: 500px;
	height: 500px;
	width: 90%
}

.l {
	text-align: center
}

.n {
	text-align: center
}

.n,
.r {
	border-bottom: 1px solid #ccc;
	padding: 5px;
	background-color: #fff
}

.r {
	min-width: 80px;
	border-left: 1px dashed #ccc
}

.name,
.r {
	text-align: left
}

.teacher {
	font-size: 10px
}

.name {
	background-color: #fff
}

.name,
.time {
	border-left: 1px dashed #ccc;
	padding: 5px
}

.day {
	min-width: 350px;
	color: #fff
}

.nameday {
	background-color: #0070f0;
	height: 50px
}

.table {
	display: inline-block;
	min-width: 350px;
	width: 100%;
	min-height: 500px;
	height: 500px;
	margin: 20px
}

.button-time {
	width: 90%;
	background-color: #0070f0;
	margin: 20px;
	border: none;
	color: white;
	font-size: 16px;
	font-weight: 600;
	padding: 20px;
	font-family: Montserrat, sans-serif;
}
